import * as React from 'react';
import {Step} from '@here/hds-stepper';

// eslint-disable-next-line no-new
new Step();

interface HDSStepProperties extends React.HTMLAttributes<HTMLElement> {
  optional?: boolean;
  label: string;
}

const HDSStep = React.forwardRef((props: HDSStepProperties, ref) => {
  const {children, optional, className, label, ...rest} = props;
  return (
    <hds-step
      {...(className && {class: className})}
      label={label}
      {...(optional && {optional})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-step>
  );
});

HDSStep.defaultProps = {
  optional: false,
  label: '',
};

export default HDSStep;
